import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmSubmenu,
  VmIcon,
  VmMenu,
  VmMenuItem 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-submenu', VmSubmenu);
define('vm-icon', VmIcon);
define('vm-menu', VmMenu);
define('vm-menu-item', VmMenuItem);
  

export interface SubmenuProps extends JSX.VmSubmenu, HTMLAttributes<HTMLVmSubmenuElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmSubmenuElement, SubmenuProps>(
  'vm-submenu',
  new Set(['label','hint','slideInDirection','active']),
);
  